.navbar {
  height: 80px;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  display: flex;
  box-shadow: 0 2px 5px #0000001a;
}

.navbar-container {
  width: 100%;
  max-width: auto;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

.navbar-logo {
  color: #333;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
}

.navbar-links {
  list-style: none;
  display: flex;
}

.navbar-links li:not(:last-child) {
  margin-right: 20px;
}

.navbar-links a {
  color: #333;
  text-decoration: none;
  transition: all .3s;
}

.navbar-links a:hover {
  color: #f1356d;
}

.product-page {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.product {
  border: 1px solid #ccc;
  border-radius: 10px;
  align-items: center;
  margin: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 5px #0000001a;
}

.product-image {
  width: 200px;
  height: 200px;
  object-fit: cover;
  margin-right: 20px;
}

.product-info {
  flex-direction: column;
  display: flex;
}

.product-name {
  margin: 0 0 10px;
  font-size: 1.5rem;
}

.product-award {
  color: #999;
  margin: 0 0 10px;
  font-size: 1rem;
}

.product-inventory {
  margin: 0 0 10px;
  font-size: 1rem;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #f1356d;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  font-size: 1rem;
}

button:hover {
  background-color: #ff4d87;
}

.shopping-cart {
  flex-direction: column;
  align-items: center;
  margin: 2rem;
  display: flex;
}

.cart-item {
  width: 100%;
  border: 1px solid #ddd;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding: 1rem;
  display: flex;
}

.cart-item img {
  width: 80px;
  margin-right: 1rem;
}

.cart-item-details {
  flex-direction: column;
  flex: 1;
  display: flex;
}

.cart-item-name {
  margin-bottom: .5rem;
  font-weight: bold;
}

.cart-item-quantity {
  color: #555;
  font-size: .8rem;
}

.cart-item-price {
  margin-left: 1rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.cart-item-actions {
  align-items: center;
  display: flex;
}

.cart-item-remove {
  color: #444;
  cursor: pointer;
  background-color: #ddd;
  border: none;
  border-radius: .3rem;
  margin-left: 1rem;
  padding: .5rem;
}

.cart-summary {
  margin-top: 2rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.cart-summary-total {
  margin-left: auto;
}

.cart-actions {
  justify-content: center;
  margin-top: 2rem;
  display: flex;
}

.cart-action-button {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: .3rem;
  margin: 0 1rem;
  padding: .5rem 1rem;
}

.cart-action-button:hover {
  background-color: #555;
}

.total-quantity {
  font: 14px;
}

* {
  margin: 0;
  padding: 0;
}

html {
  font-family: Arial, Helvetica, sans-serif;
}

